define('dummy/initializers/load-bootstrap-config', ['exports', 'dummy/config/environment', 'ember-bootstrap/config'], function (exports, ENV, Config) {

  'use strict';

  exports.initialize = initialize;

  function initialize() {
    Config['default'].load(ENV['default']["ember-bootstrap"] || {});
  }

  exports['default'] = {
    name: "load-bootstrap-config",
    initialize: initialize
  };
  /* container, application */

});