define('dummy/tests/unit/components/bs-dropdown-menu-test', ['ember-qunit'], function (ember_qunit) {

    'use strict';

    ember_qunit.moduleForComponent("bs-dropdown-menu", "BsDropdownMenuComponent", {});

    ember_qunit.test("it renders", function (assert) {
        assert.expect(2);

        // creates the component instance
        var component = this.subject();
        assert.equal(component._state, "preRender");

        // appends the component to the page
        this.append();
        assert.equal(component._state, "inDOM");
    });

    ember_qunit.test("dropdown menu has correct markup", function (assert) {
        assert.equal(this.$().prop("tagName"), "UL", "menu is an unordered list (<ul>) by default");
        assert.equal(this.$().hasClass("dropdown-menu"), true, "menu has dropdown-menu class");
    });

    ember_qunit.test("dropdown menu has correct markup", function (assert) {
        var component = this.subject({
            align: "right"
        });
        assert.equal(this.$().hasClass("dropdown-menu-right"), true, "menu has dropdown-menu-right class");
    });

    // specify the other units that are required for this test
    // needs: ['component:foo', 'helper:bar']

});