define('dummy/models/login', ['exports', 'ember', 'ember-validations'], function (exports, Ember, EmberValidations) {

    'use strict';

    exports['default'] = Ember['default'].Object.extend(EmberValidations['default'].Mixin, {
        email: null,
        password: null,
        rememberMe: false,
        validations: {
            email: {
                format: {
                    "with": /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/
                }
            },
            password: {
                length: { minimum: 3, maximum: 10 }
            }
        }
    });

});